<template>
  <v-container fluid>
    <v-data-iterator
      :items="items"
      :items-per-page.sync="itemsPerPage"
      :page="page"
      :search="search"
      :sort-by="sortBy.toLowerCase()"
      :sort-desc="sortDesc"
      hide-default-footer
    >
      <template v-slot:item.name="props">
      <v-edit-dialog :return-value.sync="props.item.name" @savedata="savedata" @cancel="cancel" @open="open"
        @close="close">
        {{ props.item.name }}
        <template v-slot:input>
          <v-text-field v-model="props.item.name" :rules="[max25chars]" label="Edit" single-line counter></v-text-field>
        </template>
      </v-edit-dialog>
      </template>
      
      <v-snackbar v-model="snack" :timeout="3000" :color="snackColor">{{ snackText }}
        <v-btn text @click="snack = false">Close</v-btn>
      </v-snackbar>
      <template v-slot:header>
        <v-toolbar
          dark
          color="blue darken-3"
          class="mb-1"
        >
          <v-text-field
            v-model="search"
            clearable
            flat
            solo-inverted
            hide-details
            
            label="Search"
          ></v-text-field>
          <template v-if="$vuetify.breakpoint.mdAndUp">
            <v-spacer></v-spacer>
            <v-select
              v-model="sortBy"
              flat
              solo-inverted
              hide-details
              :items="keys"
              
              label="Sort by"
            ></v-select>
            <v-spacer></v-spacer>
            <v-btn-toggle
              v-model="sortDesc"
              mandatory
            >
              <v-btn
                large
                depressed
                color="blue"
                :value="false"
              >
                <v-icon>mdi-arrow-up</v-icon>
              </v-btn>
              <v-btn
                large
                depressed
                color="blue"
                :value="true"
              >
                <v-icon>mdi-arrow-down</v-icon>
              </v-btn>
            </v-btn-toggle>
            <v-spacer></v-spacer>
            <!-- <v-btn
                large
                depressed
                flat 
                color="primary" 
                v-on="on"
            >
                <v-icon>mdi-pencil-plus</v-icon> buat minuman
            </v-btn> -->
            <v-dialog v-model="dialog" max-width="500px">
				<template v-slot:activator="{ on }">
					<v-btn
                    large
                    depressed
                    flat 
                    color="primary" 
                    v-on="on"
                    >
                        <v-icon>mdi-pencil-plus</v-icon> buat minuman
                    </v-btn>
				</template>
				<v-card>
					<v-card-title class="subheading font-weight-bold">{{ formTitle }}
					</v-card-title>
					<v-card-text>
						<v-container>
							<v-row>
								<v-col cols="12" sm="6" md="4">
								<v-text-field v-model="editedItem.name" label="Drink name"></v-text-field>
								</v-col>
								<v-col cols="12" sm="6" md="4">
								<v-select
                                    v-model="editedItem.type"
                                    :items="items"
                                    label="Jenis"
                                ></v-select>
								</v-col>
								<v-col cols="12" sm="6" md="4">
								<v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
								</v-col>
								<v-col cols="12" sm="6" md="4">
								<v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
								</v-col>
								<v-col cols="12" sm="6" md="4">
								<v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
								</v-col>
							</v-row>
						</v-container>
					</v-card-text>
					<v-card-actions>
						<v-spacer></v-spacer>
						<v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
						<v-btn color="blue darken-1" text @click="save">Save</v-btn>
					</v-card-actions>
				</v-card>
			</v-dialog>
          </template>
        </v-toolbar>
      </template>

      <template v-slot:default="props">
        <v-row>
          <v-col
            v-for="drink in props.drinks"
            :key="drink.name"
            cols="12"
            sm="6"
            md="4"
            lg="3"
          >
            <v-card>
              <v-card-title class="subheading font-weight-bold">{{ formTitle }}</v-card-title>

              <v-divider></v-divider>

              <v-list dense>
                <v-list-item
                  v-for="(key, index) in filteredKeys"
                  :key="index"
                >
                  <v-list-item-content :class="{ 'blue--text': sortBy === key }">{{ key }}:</v-list-item-content>
                  <v-list-item-content class="align-end" :class="{ 'blue--text': sortBy === key }">{{ drinks[key.toLowerCase()] }}</v-list-item-content>
                </v-list-item>
              </v-list>
            </v-card>
          </v-col>
        </v-row>
      </template>

      <template v-slot:footer>
        <v-row class="mt-2" align="center" justify="center">
          <span class="grey--text">Items per page</span>
          <v-menu offset-y>
            <template v-slot:activator="{ on }">
              <v-btn
                dark
                text
                color="primary"
                class="ml-2"
                v-on="on"
              >
                {{ itemsPerPage }}
                <v-icon>mdi-chevron-down</v-icon>
              </v-btn>
            </template>
            <v-list>
              <v-list-item
                v-for="(number, index) in itemsPerPageArray"
                :key="index"
                @click="updateItemsPerPage(number)"
              >
                <v-list-item-title>{{ number }}</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>

          <v-spacer></v-spacer>

          <span
            class="mr-4
            grey--text"
          >
            Page {{ page }} of {{ numberOfPages }}
          </span>
          <v-btn
            fab
            dark
            color="blue darken-3"
            class="mr-1"
            @click="formerPage"
          >
            <v-icon>mdi-chevron-left</v-icon>
          </v-btn>
          <v-btn
            fab
            dark
            color="blue darken-3"
            class="ml-1"
            @click="nextPage"
          >
            <v-icon>mdi-chevron-right</v-icon>
          </v-btn>
        </v-row>
      </template>
      <v-row>
            <v-col cols="3" v-for="drink in drinks" :key="drink.name">
            <v-card
                max-width="344"
                outlined
                class="d-inline-flex"
            >
                <v-list-item three-line>
                <v-list-item-content>
                    <div class="overline mb-4">MINUMAN</div>
                    <v-list-item-title class="headline mb-1">{{drink.name}}</v-list-item-title>
                    <v-list-item-subtitle>Type: {{drink.type}}</v-list-item-subtitle>
                    <v-list-item-subtitle>Fat: {{drink.fat}}</v-list-item-subtitle>
                    <v-list-item-subtitle>Carbs: {{drink.carbs}}</v-list-item-subtitle>
                    <v-list-item-subtitle>Protein: {{drink.protein}}</v-list-item-subtitle>
                </v-list-item-content>
                </v-list-item>

                <v-card-actions>
                    <v-icon class="mr-1" color="teal" @click="editItem(drink)">mdi-silverware</v-icon>
                    <v-icon class="mr-1" color="red"  @click="deleteItem(drink)">mdi-delete</v-icon>
                </v-card-actions>
            </v-card>
            </v-col>
        </v-row>
    </v-data-iterator>
  </v-container>
</template>

<script src="https://cdnjs.cloudflare.com/ajax/libs/vue/1.0.26/vue.js"></script>

<script>

    import{drinksRef} from '../firebase';

    export default {
    data: () => ({
        cek: -1,
        dialog: false,
        checkDelete:[],
        itemsPerPageArray: [4, 8, 12],
        search: '',
        filter: {},
        sortDesc: false,
        page: 1,
        itemsPerPage: 4,
        sortBy: 'name',
        keys: [
          'Name',
          'Jenis',
          'Fat',
          'Carbs',
          'Protein',
        ],
        items: [
            'Es',
            'Panas',
        ],
        headers: [
            {
            text: "Drink (100g serving)",
            align: "left",
            sortable: false,
            value: "name"
            },
            { text: "Type", value: "type" },
            { text: "Fat (g)", value: "fat" },
            { text: "Carbs (g)", value: "carbs" },
            { text: "Protein (g)", value: "protein" },
            { text: "Actions", value: "action", sortable: false }
        ],
        drinks: [],
        editedIndex: -1,
        editedItem: {
            name: "",
            type: "",
            fat: 0,
            carbs: 0,
            protein: 0
        },
        defaultItem: {
            name: "",
            type: "",
            fat: 0,
            carbs: 0,
            protein: 0
        }
    }),

    firebase: {
        drinks : drinksRef
    },

    computed: {
      numberOfPages () {
        return Math.ceil(this.drinks.length / this.itemsPerPage)
      },
      filteredKeys () {
        return this.keys.filter(key => key !== `Name`)
      },
      formTitle() {
        return this.cek === -1 ? "New Item" : "Edit Item";
      }
    },

    watch: {
        dialog(val) {
            val || this.close();
        }
    },

    methods: {
      nextPage () {
        if (this.page + 1 <= this.numberOfPages) this.page += 1
      },
      formerPage () {
        if (this.page - 1 >= 1) this.page -= 1
      },
      updateItemsPerPage (number) {
        this.itemsPerPage = number
      },
      editItem(item) {
            this.editedIndex = item['.key']
            this.cek = 0
            this.editedItem = Object.assign({}, item)
            this.dialog = true
        },
        deleteItem(item) {
            confirm('Are you sure you want to delete this item?') && drinksRef.child((item['.key'])).remove()
        },
        multipleDelete(){
            for(var i=0;i<this.checkDelete.length;i++){
                drinksRef.child(this.checkDelete[i]).remove()
            }
            this.checkDelete=[]
        },
        close() {
            this.dialog = false;
            setTimeout(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
            }, 300)
        },
        save() {
            if (this.cek > -1) {
                drinksRef.child(this.editedIndex).set({name: this.editedItem.name,type: this.editedItem.type, fat: this.editedItem.fat, carbs: this.editedItem.carbs, protein: this.editedItem.protein})
                this.clear()
            } else {
                drinksRef.push({name: this.editedItem.name,type: this.editedItem.type, fat: this.editedItem.fat, carbs: this.editedItem.carbs, protein: this.editedItem.protein});
                this.clear()
            }
            this.close();
        },
        clear(){
            this.editedItem={}
            this.editedItem.name= ''
            this.editedItem.type= ''
            this.editedItem.fat= 0
            this.editedItem.carbs= 0
            this.editedItem.protein= 0
            this.cek=-1
        }
    },
  }
</script>